import EditMenu from '@/components/global/editMenu/EditMenu'
import YAlert from '@/components/global/yAlert/YAlert'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
export default {
    name: 'locataire',
    components: {
        EditMenu,
        YAlert,
        LLoading1
    },
    data () {
        return {
            loading: false,
            newForm: false,
            newLogin: '',
            newPassword: '',
            newAdmin: false,
            newTel:'',
            comptes: [],
            //------- modifier ----------
            form: false,
            password: '',
            login: '',
            tel:''
        }
    },
    methods: {
        add () {
            if(this.newLogin == '' || this.newPassword == '' || this.newTel == '') {
                this.$refs.yAlert.display('Veuillez remplir les champs')
                return
            }
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type: 'addCompte',
                login: this.newLogin,
                password: this.newPassword,
                tel: this.newTel,
                isAdmin: this.newAdmin === true ? 1 : 0
            })
            .then(({data}) => {
                this.loading = false
                if(data == 'exist') {
                    this.$refs.yAlert.display('Données déjà prix')  
                }
                else {
                    this.$refs.yAlertS.display('Fait !')
                    this.comptes = data
                    //------ reinitialize ----------------------
                    this.newLogin = ''
                    this.newAdmin = false
                    this.newPassword = ''
                    this.newForm = false
                }
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        getCompte () {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'get',
            })
            .then(({data}) => {
                this.comptes = data
                this.loading = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        modify () {
            if(this.login == '' || this.password == '' || this.tel == '') {
                this.$refs.yAlert.display('Veuillez remplir les champs')
                return
            }
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'modify',
                login: this.login,
                tel:this.tel,
                password: this.password
            })
            .then(({data}) => {
                this.loading = false
                if(data == 'exist') {
                    this.$refs.yAlert.display('Données déjà prix')  
                }
                else {
                    this.$store.commit('loginCommit',{login:this.login,isAdmin:1})
                    //--- closure ----------
                    this.login = ''
                    this.password = ''
                    this.form = false
                    this.$refs.yAlertS.display('Fait !')
                }
                
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
        deleteCompte (id) {
            this.loading = true
            const axios = require('axios')
            axios.post(this.$store.state.baseUrl + 'inscription.php', {
                type:'delete',
                id: id
            })
            .then(({data}) => {
                this.comptes = data
                this.loading  = false
            })
            .catch((error) => {
                console.log(error)
                this.loading = false
            })
        },
    },
    mounted () {
        this.getCompte ()
    }
}