import LDropDown from '@/components/forms/lDropDown/LDropDown.vue'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import YAlert from '@/components/global/yAlert/YAlert'
import YesNo from '@/components/global/yesNo/YesNo1'
export default {
  name: 'modifyLocataire',
  components: {
    LDropDown,
    LLoading1,
    YAlert,
    YesNo
  },
  data () {
    return {
      Display: false,
      loading: false,
      loyer:0,
      dateDebut: '',
      contrat: []
    }
  },
  methods: {
    display(contrat) {
      this.contrat = contrat
      this.loyer = this.contrat.loyer
      this.Display = true
    },
    close () {
      this.loyer = ''
      this.Display = false
    },
    showYesNo () {
      this.$refs.yesNo.display('Voulez vous vraiment éffectuer cette opération ? toutes les données du contrat seront reinitialisées')
    },
    yesNoAction (a) {
      if(a=='yes') this.publish()
    },
    validation () {
      let dn = new Date(this.$store.state.load.serverDate)
      let db = new Date(this.dateDebut)
      let test = db.getTime() - dn.getTime()
      return this.loyer === '' || this.loyer < 0 || isNaN(test)
    },
    publish () {
      if(this.validation()) {
        this.$refs.yAlert.display('Veuillez bien remplir les champs!')
        return
      }
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'contrat.php', {
        type: 'modify',
        loyer: this.loyer,
        id: this.contrat.id,
        dateDebut: this.dateDebut
      })
      .then(() => {
        this.loadData()
      })
      .catch((error) => {
        alert(error)
        this.loading = false
      })
    },
    loadData () {
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'update.php', {
        type:'get'
      })
      .then(({data}) => {
        this.$store.commit('loadCommit',data)
        this.loading = false
        this.$refs.yAlertS.display('Enregistré!')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
