import html2pdf from 'html2pdf.js'
import { Filesystem, Directory, Encoding } from '@capacitor/filesystem';
export default {
    name: 'facture',
    data () {
        return {
            payment: [],
            contrat: [],
            dette: 0
        }
    },
    methods: {
        print () {
            let element = document.getElementById('toPrint')
            var opt = {
              margin:       1,
              filename:     'myfile.pdf',
              image:        { type: 'jpeg', quality: 0.98 },
              html2canvas:  { scale: 2, width:1366, windowWidth:1366 },
              jsPDF:        { unit: 'in', format: 'a3', orientation: 'portrait' }
            }
            /*html2pdf().from(element).set(opt).output('datauristring').then((output) => {
                this.$refs.lien.href = output
                this.$refs.lien.click()
            })*/
            html2pdf().from(element).set(opt).toPdf().outputPdf().then(function (pdfAsString) {
                // The PDF has been converted to a Data URI string and passed to this function.
                // Use pdfAsString however you like (send as email, etc)! For instance:
                //console.log(pdfAsString)
                let blob = pdfAsString
                //let blob = this.dataURItoBlob(pdfAsString)
                Filesystem.writeFile({
                    path: 'secrets/text',
                    data: blob,
                    directory: Directory.Documents,
                    encoding: Encoding.UTF8,
                }).then((response) => {
                    alert(response)
                    alert('Sauvegardé dans Ducuments')
                    })
                    .catch((error) => {
                    alert(error)
                    })
            });
        },
        setPayment() {
            let payments = this.$store.state.load.payments
            let i, id = this.$route.params.id, max = payments.length
            for(i=0; i<max; i++) {
                if(payments[i].id == id) {
                    this.payment = payments[i]
                    break
                }
            }
        },
        setContrat () {
            let contrats = this.$store.state.load.contrats
            let i, id = this.payment.contratLocation, max = contrats.length
            for(i=0; i<max; i++) {
                if(contrats[i].id == id) {
                    this.contrat = contrats[i]
                    break
                }
            }
        },
        expiration (contrat) {
            let a = new Date(contrat.date_debut)
            let total = contrat.totalPayer + contrat.totalCharge
            let monthPaid = (total - total%contrat.loyer)/contrat.loyer
            let day = a.getDate()
            a.setMonth(a.getMonth()+monthPaid + 1)
            if(day != a.getDate()) {
                a.setMonth(a.getMonth()-1)
                a = this.endMonth(a)
            }
            let year = a.getFullYear()
            let month = a.getMonth()+1
            day = a.getDate();

            month = (month < 10)? '0'+ month : month;
            day= (day < 10)? '0' + day : day;

            let dn = new Date(this.$store.state.load.serverDate)
            let expire = a.getTime() < dn.getTime() ? ' (expiré)' : ''

            return day + ' ' + this.$store.state.mois[month -1] + ' ' + year + expire
        },
        exit () {
            if(this.$route.params.via == 'recap') this.$router.go(-1)
            else this.$router.push('/menu')
        }
    },
    mounted () {
        //let n = new Date(this.$store.state.load.serverDate)
        this.setPayment()
        this.setContrat()
        this.dette = this.expiration(this.contrat)
    }
}