<template>
  <div class="recap">
    <div style="z-index:1" class="lPBFCTitle">
        <div class="lPBFCCross" @click="goBack"><img src="@/assets/Retour.png"/></div>
        Recap
    </div>
    <div class="recapBody">
      <div class="recapVisible">
          <span v-for="(prop,index) in display" :key="index">{{index}}
            <input v-model="display[index]" type="checkbox"/>
          </span>
      </div>
      <div class="recapTitle">
        Recapitulatif de 
        <span style="color:rgb(244,10,10)">{{ moisC }} {{annee}}</span> 
        pour <span style="color:rgb(10,10,244)">{{ citeC }}</span>
      </div>
      <div :style="{width:width}" class="recapEltHead">
        <div class="recapLoc">Locataire</div>
        <div v-show="display.cite">Cite</div>
        <div v-show="display.chambre">Chambre</div>
        <div>Payements</div>
        <div v-show="display.charge">Charge/Déch..</div>
        <div>Dette restante</div>
        <div v-show="display.avance">Avance </div>
        <div v-show="display['mois restant']">Mois restant</div>
        <div v-show="display['mois impayé']">Mois impayé</div>
      </div>
      <div :style="{width:width}" v-for="(ct,index) in contrats" :key="index" class="recapElt">
        <div class="recapLoc">
          <span @click="$router.push({name: 'locataire', params: {locId:ct.locataire}})">
          {{ ct.locataireNom + ' ' +ct.locatairePrenom }}
          </span>
        </div>
        <div v-show="display.cite">{{ ct.citeNom }}</div>
        <div v-show="display.chambre">{{ ct.chambreNom }} ({{ ct.loyer }}/mois)</div>
        <div class="recapPay">
          <span v-for="(pay,index1) in getPayment(ct.payments)" :key="index1">
             - le {{ pay.day + '/' + pay.month }} : {{ pay.montant }}FCFA
            <div @click="toFacture(pay.id)" class="recapFacture"> facture </div>
          </span>
          <span v-show="getPayment(ct.payments).length === 0 " >Aucun</span>
        </div>
        <div v-show="display.charge" class="recapPay">
          <span v-for="(char,index1) in getContrat(ct.charges)" :key="index1">
             - Raison : {{ char.reason }}
             - montant : {{ char.montant }}FCFA
          </span>
          <span v-show="getContrat(ct.charges).length === 0 " >Aucun</span>
        </div>
        <div>{{ dette(ct.balance) }}</div>
        <div v-show="display.avance">{{ avance(ct.balance) }}</div>
        <div v-show="display['mois restant']">{{ moisRestant(ct.balance,ct.loyer) }}</div>
        <div v-show="display['mois impayé']">{{ moisImpayer(ct.balance,ct.loyer) }}</div>
      </div>
      <div :style="{width:width}" class="recapElt recapEltLast">
        <div class="recapLoc">Total</div>
        <div v-show="display.cite"> - </div>
        <div v-show="display.chambre"> - </div>
        <div> {{ totalPayment }} FCFA</div>
        <div v-show="display.charge">{{totalCharge}} FCFA</div>
        <div>{{ totalDette() }} FCFA</div>
        <div v-show="display.avance">{{ totalAvance() }} FCFA</div>
        <div v-show="display['mois restant']">{{ totalMoisR() }}</div>
        <div v-show="display['mois impayé']" 
          style="border-bottom-right-radius: 0.5em;">{{ totalMoisI() }}
        </div>
      </div>
    </div>
  </div>
</template>
<script src="./recap.js">
</script>
<style src="./recap.css">
</style>